import * as React from 'react';
import Granim from '~src/components/Granim/Granim';
import { granimStates } from '~src/components/Granim/granim_options';
import * as GranimTypes from '~src/components/Granim/__types__/index';
import { ThemeContext } from '~src/context/Context/ThemeContext';
import * as styles from './GranimSSR.module.scss';

export const GranimSSR = () => {
	const granimRef = React.useRef<GranimTypes>();
	const { theme } = React.useContext(ThemeContext);
	React.useEffect(() => {
		if (typeof window !== 'undefined') {
			granimRef?.current && granimRef?.current?.changeState(theme);
			return () => {
				//	console.log('cleanup');
				granimRef?.current?.destroy();
			};
		}
	}, [theme]);
	if (typeof window !== 'undefined') {
		return (
			<Granim
				className={styles.granimClass}
				//style={{ zIndex: -1 }}
				states={granimStates}
				direction="diagonal"
				isPausedWhenNotInView
				granimRef={granim => (granimRef.current = granim)}
			/>
		);
	}
	return <span />;
};
