import * as React from 'react';
import Footer from './Footer/Footer';
import DocumentHead from './Metadata/DocumentHead';
//import { BodyClass } from '~src/components/Helpers/Classname';
//import Header from '~src/components/Layout/Header/SiteHeader';
import Subscribe from '~src/components/Layout/Subscribe/SubscribeOverlay';
import FramerWrapper from '~src/context/Context/FramerContext';
import SubscribeNotification from './Subscribe/SubscribeNotification';
import * as styles from './Layout.module.scss';
import { StickyNavigation } from './Navigation/Navigation';

import { GranimSSR } from '~src/components/Granim/GranimSSR';

const BodyClass = (template: string): string => {
	const classes = [];

	if (template === 'home') {
		//classes.push(styles2.homeTemplate);
	} else if (template === 'post') {
		classes.push(styles.postTemplate);
	} else if (template === 'page') {
		classes.push(styles.postTemplate);
	} else if (template === 'author') {
		//	classes.push(`author-template`);
	}

	return classes.join(` `).trim();
};

const Layout: React.FC<LayoutProps> = ({ children, template, stickyRef, data, header }) => {
	const bodyClass = BodyClass(template);

	return (
		<div>
			<DocumentHead className={bodyClass} template={template} data={data} />
			<div className={styles.siteWrapper}>
				{typeof window !== 'undefined' && <GranimSSR />}
				{template === 'home' && header}
				<FramerWrapper className={styles.framerWrapper}>
					{template !== 'home' && header}
					<main
						//ref={template !== 'post' ? stickyRef : null}
						id="site-main"
						className={styles.siteMain}>
						{children}
					</main>
					{/* 	{template === 'post' && readNext} */}
				</FramerWrapper>
				<StickyNavigation data={data} stickyRef={stickyRef} template={template} />
				<Footer />
				<SubscribeNotification />
				<Subscribe />
			</div>
		</div>
	);
};

export default Layout;

interface LayoutProps {
	template?: string;
	stickyRef?: React.RefObject<HTMLElement>;
	data?: unknown;
	header?: JSX.Element;
}
