const darkGradients = [
	['#000', '#022248'],
	['#000', '#023707'],
	['#000', '#3b0101'],
	['#030E32', '#000'],
	['#200122', '#000'],
];

const lightGradients = [
	['#fff', '#E1F4FB'],
	['#fff', '#ffdde1'],
	['#E0EAFC', '#fff'],
];

function shuffle(a: string[][]) {
	let j: number, x: string[], i: number;
	for (i = a.length - 1; i > 0; i--) {
		j = Math.floor(Math.random() * (i + 1));
		x = a[i];
		a[i] = a[j];
		a[j] = x;
	}
	return a;
}
export const granimStates = {
	'default-state': {
		gradients: shuffle(darkGradients),
		transitionSpeed: 5000,
	},
	dark: {
		gradients: shuffle(darkGradients),
		transitionSpeed: 5000,
	},
	light: {
		gradients: shuffle(lightGradients),
		transitionSpeed: 5000,
	},
};
