import * as React from 'react';
import * as PropTypes from 'prop-types';
import Link from 'gatsby-link';
import { LocaleContext } from '~src/context/Context/LocaleContext';
import { locales } from '~src/_i18n/i18n';
import { LinkInterface } from '~src/types/intl';

// Use the globally available context to choose the right path
const LocalizedLink: React.FC<LinkInterface> = ({ to, locale, onClick, ...props }) => {
	const { activeLocale /* , setLocale */ } = React.useContext(LocaleContext);
	const targetLocale = locale ? locales[locale] : locales[activeLocale];

	const handleClick = e => {
		/* SEEMS UNNECESSARY: test and remove
		if (locale) {
		 setLocale(locale)
		}
		*/
		if (onClick) {
			onClick(e);
		}
	};

	const isIndex = to === `/`;

	// If it's the default language, don't do anything
	// If it's another language, add the "path"
	// However, if the homepage/index page is linked don't add the "to"
	// Because otherwise this would add a trailing slash
	const path = targetLocale.default ? to : `/${targetLocale.path}${isIndex ? `` : `${to}`}`;

	return <Link onClick={handleClick} {...props} to={path} />;
};

LocalizedLink.propTypes = {
	children: PropTypes.node,
	to: PropTypes.string,
	locale: PropTypes.string,
};

export default LocalizedLink;
