import * as React from 'react';
import { FormatDateOptions, useIntl } from 'react-intl';
import { LocaleContext } from '~src/context/Context/LocaleContext';

const useIntlHelper = () => {
	const { activeLocale, locales } = React.useContext(LocaleContext);
	const intl = useIntl();
	const formatMessage = (id: string) => intl.formatMessage({ id: id });
	const formatDate = (date: string | number | Date, options?: FormatDateOptions) =>
		options ? intl.formatDate(date, options) : intl.formatDate(date, locales[activeLocale].formatOptions);
	return { intl, activeLocale, locales, formatMessage, formatDate };
};

export default useIntlHelper;
