import * as React from 'react';
import * as PropTypes from 'prop-types';
import Granim from 'granim';
import * as GranimTypes from './__types__/index';

const ReactGranim: React.FC<GranimProps> = (props = defaultProps) => {
	const style: React.CSSProperties = {
		position: 'absolute',
		display: 'block',
		width: '100%',
		height: '100%',
		top: 0,
		right: 0,
		bottom: 0,
		left: 0,
	};

	React.useEffect(() => {
		const config = {
			element: `#${props.id}`,
			states: {
				[props.defaultStateName]: {
					gradients: [
						['#8af094', '#ffd1ff'],
						['#f6d365', '#fda085'],
						['#84fab0', '#8fd3f4'],
						['#fa709a', '#fee140'],
						['#4facfe', '#00f2fe'],
						['#43e97b', '#38c9f7'],
						['#f83600', '#f9d423'],
					],
					transitionSpeed: 5000,
				},
			},
		};
		const granim: GranimTypes = new Granim(Object.assign({}, config, props));

		//console.log('initial render');
		if (props.granimRef) {
			props.granimRef(granim);
		}
		return granim !== null && typeof window == 'undefined' && granim.clear();
	}, [props]);

	const { id, className } = props;
	return <canvas className={className} id={id} style={props.style || style} />;
};

ReactGranim.propTypes = {
	className: PropTypes.string,
	defaultStateName: PropTypes.string,
	direction: PropTypes.oneOf(['diagonal' || 'left-right' || 'top-bottom' || 'radial']),
	elToSetClassOn: PropTypes.string,
	element: PropTypes.string,
	granimRef: PropTypes.func,
	id: PropTypes.string,
	isPausedWhenNotInView: PropTypes.bool,
	stateTransitionSpeed: PropTypes.number,
	states: PropTypes.objectOf(
		PropTypes.shape({
			gradients: PropTypes.oneOfType([
				PropTypes.arrayOf(
					PropTypes.shape({
						color: PropTypes.string,
						pos: PropTypes.number,
					}),
				),
				PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
			]),
			transitionSpeed: PropTypes.number,
		}),
	),
	style: PropTypes.object,
};

const defaultProps: GranimProps = {
	//id: `granim-canvas-${Math.random().toString(36).substring(2)}`,
	id: `granim-canvas-background`,
	defaultStateName: 'default-state',
	isPausedWhenNotInView: true,
};

// Same approach for defaultProps too
ReactGranim.defaultProps = defaultProps;

type GranimProps = {
	id?: string;
	className?: string;
	element?: string;
	name?: string;
	states?: {
		[key: string]: {
			transitionSpeed?: number;
			gradients?: { color: string; pos: number }[][] | string[][] | any;
		};
	};
	style?: React.CSSProperties;
	defaultStateName?: string;
	elToSetClassOn?: string;
	direction?: 'diagonal' | 'left-right' | 'top-bottom' | 'radial';
	isPausedWhenNotInView?: boolean;
	stateTransitionSpeed?: number;
	granimRef?: (granim) => void;
};

export default ReactGranim;
