import * as React from 'react';
import SunIcon from '~src/_assets/icons/theme/SunIcon';
import MoonIcon from '~src/_assets/icons/theme/MoonIcon';
//import { Moon as MoonIcon } from '@styled-icons/fa-solid/Moon';
//import { WbSunny as SunIcon } from '@styled-icons/material-twotone/WbSunny';

import { ThemeContext } from '~src/context/Context/ThemeContext';
import * as styles from '../Navigation.module.scss';

export const Switch = ({ theme }) => {
	return theme === 'light' ? <MoonIcon /> : <SunIcon />;
};

const ThemeSwitch: React.FC = () => {
	const { theme, setTheme } = React.useContext(ThemeContext);

	React.useEffect(() => {
		//return () => {};
	}, [theme]);

	if (typeof window == 'undefined') {
		return <div className={`${styles.socialLink}`} title="Switch Theme"></div>;
	}

	return (
		<div className={`${styles.socialLink}`} title="Switch Theme" onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}>
			<Switch theme={theme} />
		</div>
	);
};

export default ThemeSwitch;
